<template>
  <div class="setUp">
        <div class="top"></div>
        <div class="setUp-box">
            <van-nav-bar
            title="设置"
            left-text="返回"
            left-arrow
            @click-left="onClickLeft"
            />
        </div>
        <div>
            <van-cell class="interval" title="账号与安全" is-link />
            <van-cell class="interval" title="隐私" is-link />
            <van-cell class="interval" title="通用" is-link />
            <!-- <van-cell class="interval" title="切换账号" is-link /> -->
            <van-cell class="interval" :title="userInfo.loginText" is-link  @click="login"/>
        </div>
  </div>
</template>

<script>
import {createNamespacedHelpers} from 'vuex';

const {mapState} = createNamespacedHelpers('setUp');

export default {
    name: 'setUp',

    computed: {
        ...mapState(['userInfo'])
    },

    methods: {
        // 返回上一步
        onClickLeft() {
            this.$router.go(-1);
        },

        //退出或者登录
        login() {
            // return;
            let userStatus = localStorage.getItem('user');
            // console.log('userStatus :', userStatus);

            if (userStatus) {
                let userInfo = JSON.parse(userStatus);
                // console.log('userInfo :', userInfo);

                if (userInfo.isLogin) {
                    // console.log('userInfo.isLogin :', userInfo.isLogin);

                    localStorage.removeItem('user');

                    this.$store.commit('setUp/changeUserInfo', {loginText: '登录'});
                    this.$router.push({name: 'login'});
                } else {
                    this.$router.push({name: 'login'});
                }
            } else {
                this.$router.push({name: 'login'});
            }
        }
    },

    created() {

        // console.log('mine created');

        //获取用户信息
        let userStatus = localStorage.getItem('user');
        // console.log('userStatus :', userStatus);

        if (userStatus) {
            let users = JSON.parse(userStatus);

            if (users.isLogin) {

                this.$store.commit('setUp/changeUserInfo', {loginText: '退出'});

            }
        }
    }
}
</script>

<style lang="less" scoped>
    .setUp {
        font-size: 14px;
        .top {
            height: 24px;
            background-color: #000;
        }
        .setUp-box {
            margin-bottom: 10px;
        } 
        .van-nav-bar__title {
            font-size: 20px;
            font-weight: 600;
        }

        .interval {
            // margin-bottom: 4px;
            font-size: 16px;
            border-bottom: 2px solid #ccc; 
        }
    }
</style>